import React from 'react';
import { Row, Col, Popconfirm } from 'antd';


export interface GroupNameItemProps {
  /**  */
  type: string;
  /** (  )  */
  count: number;
  /**  `` */
  onRenameGroup?: Function;
  /**  `` */
  onDeleteGroup?: Function;
}

/**
 * item
 */
export default function GroupNameItem({
  type,
  count,
  onRenameGroup,
  onDeleteGroup
}: GroupNameItemProps) {

  function handleRenameGroup() {
    onRenameGroup && onRenameGroup()
  }

  function confirm() {
    onDeleteGroup && onDeleteGroup()
  }

  return (
    <Row type="flex" style={{ marginTop: '10px', padding: '0 8px', lineHeight: '40px', background: '#f9f9f9' }}>
      <Col span={6}>
        <span style={{ color: '#222' }}>{type}</span>
        <span>{count}</span>
      </Col>
      <Col span={18} style={{ textAlign: 'right' }}>
        <span
          onClick={handleRenameGroup}
          className="primary-color cursor-pointer"
          style={{ padding: '0 12px' }}>
          
        </span>
        {
          count === 0 ? (
            <Popconfirm placement="left" title="" onConfirm={confirm} okText="" cancelText="">
              <span
                className="primary-color cursor-pointer"
                style={{ padding: '0 12px' }}
              >
                
              </span>
            </Popconfirm>
          ) : null
        }
      </Col>
    </Row>
  )
}